<template>
  <div class="col-12 no_x" v-if="ready">
    <div style='display:flex;flex-direction:row'>
        <div class="col-3">
            <div style='display:flex;flex-direction:row'>
                <div class="col-12">
                    <label class="form-control-label">User Title</label>
                    <select v-model="type" class='form-control select' required @change="new_list">
                            <option v-for="t in type_list">{{ t }}</option>
                    </select>
                </div>
            </div>           
        </div> 
        <!---->
        <div class="col-6">
          <VueSignaturePad
              id="signature"
              width="100%"
              height="200px"
              ref="signaturePad"
            />
            <div style='margin-top:0.5em;margin-left:30%;margin-right:30%'>
              <button
                class="btn btn-outline-secondary float-right"
                @click="undo"
              >
                Undo
              </button>
              <button
                class="btn btn-outline-primary float-left"
                @click="save"
              >
                Save
              </button>
            </div>
        </div>   
        </div> <br/> 
        <div class="col-3"></div>
        <!---->         
        <div class="col-12" style="overflow:auto">
            <div  class="col-12"><iframe :src="url" style="background: #FFFFFF;width:100%;height:30em;"></iframe>
                <br/><br/>
            </div>              
        </div>
    
  </div>
</template>

<script>
function getURLParameter(name) {
  return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.search) || [null, ''])[1].replace(/\+/g, '%20')) || null;
}
export default {
    data(){
        return{
            pos:'TAC Member',
            new_user:false,
            type_list:['Principal'],
            name:'',
            userlist:[],
            type:'Principal',
            proc_id:'',
            pwd:'',
            pwd_check:'',
            return:'',
            url:'',
            name_select:'new_user',
            ready:false,
            params: window.location.href.substr(window.location.href.indexOf('?')),
        }
    },
async created(){
    await axios.get('../user_info.php')
          .then(
              response=>{
                  this.userlist = response.data
                  this.name = this.userlist[1];
              }
          );
    let param = new URLSearchParams(this.params);
     if(param.has('id')){
       this.proc_id = param.get('id');
       console.log(this.proc_id);      
            }      
    var baseurl = "src/Procurement/procurement/backend/ret_stat.php?p_id=";
    this.url = baseurl + this.proc_id;
    //onload data
    var formData = new FormData();
    formData.append('type',this.type);
    await axios.post('Procurement/procurement/backend/extra_userlist.php',formData)
        .then(
            (response)=>{
                this.userlist = response.data;
            }
        )


    this.ready = true;
},
    
  name: 'user_setup',
  methods: {
      new_list(){
          var formData = new FormData();
            formData.append('type',this.type);
            axios.post('Procurement/procurement/backend/extra_userlist.php',formData)
                .then(
            (response)=>{
                this.userlist = response.data;
                this.name_select='';
            }
        )
      },
    undo() {
      this.$refs.signaturePad.undoSignature();
      this.pwd='';
      this.pwd_check='';
    },
    save() {
      const { isEmpty, data } = this.$refs.signaturePad.saveSignature();
      if(isEmpty){
          this.$alert('Signature board cannot be empty!');
        } else if(this.name_select==''){
            this.$alert('Please Select your name below ');
          } else if((this.name_select=='new_user')&&(this.name=='')){
            this.$alert('Please Enter your name ');
          } else {
          var sign = data;
          var formData = new FormData();
          formData.append('sign',data);
          formData.append('name',this.name);
          formData.append('name_select',this.name_select);
          formData.append('type',this.type);
          formData.append('pos',this.pos);
          formData.append('proc_id',this.proc_id);
          axios.post('Procurement/procurement/backend/sign_tender.php',formData)
            .then(
              (response)=>{
                this.$alert('Successfully Signed, return to previous form ...');
                window.location.href=this.url;
              }
            )
          
        }     
    }
  }
};
</script>

<style>
#signature {
  border: double 3px transparent;
  border-radius: 5px;
  background-image: linear-gradient(white, white),
    radial-gradient(circle at top left, #4bc5e8, #9f6274);
  background-origin: border-box;
  background-clip: content-box, border-box;
}
</style>



